import React, { useEffect } from "react";
import { Nav, Navbar } from "react-bootstrap";
import { motion } from "framer-motion";
import Avatar from "../assets/avatar.jpg";
import ScrollspyNav from "react-scrollspy-nav";
import styled from "styled-components";

const ButtonLight = styled(({ alt, ...props }) => (
  <img id="theme-img" alt={alt} {...props} />
))`
  width: 15px;
  margin: 0 auto;
  padding-top: 1rem;
  cursor: pointer;

  @media (max-width: 1024px) {
    margin: 0;
  }
`;

export default function Navigation() {
  const navItems = [
    {
      name: "About",
      href: "#about",
    },
    {
      name: "Skills",
      href: "#skills",
    },
    {
      name: "Portfolio",
      href: "#portfolio",
    },
    {
      name: "Codepen",
      href: "#codepen",
    },
    {
      name: "Contact",
      href: "#contact",
    },
  ];

  const sectionIds = navItems.map((item) => item.href.replace("#", ""));

  const handleThemeToggle = (event) => {
    if (event.target.src.includes("/pic_bulboff.gif")) {
      window.computeDarkTheme(false);
    } else {
      window.computeDarkTheme(true);
    }
  };
  return (
    <Navbar
      collapseOnSelect
      variant="dark"
      expand="lg"
      className="fixed-top"
      id="sideNav"
    >
      <Navbar.Brand onClick={() => (window.location = "/")}>
        <span className="d-none d-lg-none">Aashish Mahajran</span>
        <span className="d-block d-lg-block nav-img">
          <motion.img
            className="img-fluid img-profile rounded-circle mx-auto mb-2"
            src={Avatar}
            alt="Aashish Maharjan"
            drag
            dragConstraints={{ left: 0, top: 0, right: 0, bottom: 0 }}
            dragElastic={0.1}
          />
        </span>
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />

      <Navbar.Collapse id="responsive-navbar-nav">
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Nav className="mr-auto">
          <ScrollspyNav
            scrollTargetIds={sectionIds}
            offset={200}
            activeNavClass="active"
            scrollDuration="100"
            headerBackground="true"
          >
            {navItems.map((navItem, index) => (
              <Nav.Link
                href={navItem.href}
                key={index}
                onClick={(event) => {
                  window.location = `/${navItem.href}`;
                }}
              >
                {navItem.name}
              </Nav.Link>
            ))}
          </ScrollspyNav>
          <ButtonLight src="/pic_bulbon.gif" onClick={handleThemeToggle} />
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
}
